<template>
    <div class="todo-app__tasks d-flex flex-column align-items-center">
        <!-- Progress -->
        <div class="todo-app__tasks-progress d-flex flex-column">
            <!-- Total -->
            <div class="todo-app__tasks-progress__total">
                <label for="total-tasks">
                    <span class="title">Total Tasks:</span>
                    <span>{{ todos.length }}</span>
                </label>
            </div>
            <!-- Total -->

            <!-- In Progress -->
            <div class="todo-app__tasks-progress__in-progress">
                <label for="in-progress-tasks">
                    <span class="title">In Progress:</span>
                    <span>{{ in_progress_tasks }}</span>
                </label>
            </div>
            <!-- In Progress -->

            <!-- Complete -->
            <div class="todo-app__tasks-progress__complete">
                <label for="complete-tasks">Completed:</label>
                <div class="progress">
                    <div class="progress-bar" id="done-tasks" :style="{width: completed_percent}" 
                         role="progressbar" :aria-valuenow="completed_tasks" aria-valuemin="0" :aria-valuemax="todos.length"
                    >
                        <span v-if="todos.length > 0">{{ completed_percent }}</span>
                        <span v-else></span>
                    </div>
                </div>
            </div>
            <!-- Complete -->
        </div>
        <!-- Progress -->

        <!-- Tasks List -->
        <ul class="todo-app__tasks-list pl-0 d-flex flex-column align-items-center">
            <!-- Tasks List items -->
            <li v-for="(todo, index) in filter_todos" :key="index"
                class="todo-app__tasks-list__item"
                :class="{completed: todo.completed}"
            >
               <input type="checkbox" name="completed-task" v-model="todo.completed">
               <input type="text" class="item"
                      v-model="todo.content"
                      @blur="done_edit(todo, index)"
                      @keydown.enter="done_edit(todo, index)"
                      disabled
               >
               <div class="created-at">
                   {{ todo.created_at }}
               </div>
               <div class="modify-tasks">
                   <div class="edit-tasks"
                        @click="edit_todo(todo, index)"
                        v-b-tooltip.hover.bottom="{ variant: 'light'}" title="Edit"
                   >
                       <font-awesome-icon icon="fa-solid fa-pen-to-square" />
                   </div>
                   <div class="destroy-tasks"
                        @click="remove_todo(todo)"
                        v-b-tooltip.hover.bottom="{ variant: 'light'}" title="Remove"
                   >
                       <font-awesome-icon icon="fa-solid fa-xmark" />
                   </div>
               </div>
            </li>
        </ul>
        <!-- Tasks List -->
    </div>
</template>

<script>
    import $ from 'jquery';
    export default {
        props: [
            'task_data',
            'filter_data'
        ],
        data() {
            return {
               todos: this.task_data,
               in_progress_tasks: 0,
               completed_tasks: 0,
               completed_percent: null,
               edited_task: null,
            }
        },
        mounted() {
            this.count_tasks();
            this.progress();
            this.infinite_scroll();
        },
        updated() {
            this.count_tasks();
            this.progress();
            this.infinite_scroll();
            localStorage.setItem('tasks', JSON.stringify(this.todos));
        },
        computed: {
            // Fillter todos
            filter_todos() {
               if(this.filter_data === 'All Tasks') 
               {
                   return this.todos;
               }
               else if(this.filter_data === 'In-Progress Tasks')
               {
                   return this.todos.filter((todo) => {
                       return !todo.completed;
                   });
               }
               else if(this.filter_data === 'Completed Tasks')
               {
                   return this.todos.filter((todo) => {
                       return todo.completed;
                   });
               }
               else if(this.filter_data === 'By date (Newest)')
               {
                  return this.sort_by_date('newest');
               }
               else
               {
                   return this.sort_by_date('oldest');
               }
            }
        },
        methods: {
            // Infintie Scroll
            infinite_scroll() {
                const tasks = $('.todo-app__tasks-list__item');
                const observer = new IntersectionObserver(entries => {
                    entries.forEach(entry => {
                       entry.target.classList.toggle('show', entry.isIntersecting);
                    //    if(entry.isIntersecting) observer.unobserve(entry.target);
                    });
                });

                for(let i = 0; i < tasks.length; i++)
                {
                    observer.observe(tasks[i]);
                }
            },

            // Count Completed/Progress Tasks
            count_tasks() {
                let progress_tasks = 0;
                let completed_tasks = 0;
                for(let i = 0; i < this.todos.length; i++)
                {
                    if(this.todos[i].completed === false)
                    {
                        progress_tasks++;
                    }
                    else
                    {
                        completed_tasks++;
                    }
                }
                this.in_progress_tasks = progress_tasks;
                this.completed_tasks = completed_tasks;
            },

            // Completed Percent
            progress() {
                let percent_per_tasks = 100 / this.todos.length;
                if(this.todos.length > 0)
                {
                    this.completed_percent = (percent_per_tasks * this.completed_tasks) + '%';
                }
                else
                {
                    this.completed_percent = 0;
                }
            },

            // Remove Todo
            remove_todo(todo) {
                this.$swal({
                    title: 'Are you sure?',
                    text: "You won't be able to revert this!",
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonColor: '#3085d6',
                    cancelButtonColor: '#d33',
                    confirmButtonText: 'Yes, delete it!'
                }).then((result) => {
                    if(result.isConfirmed)
                    {
                        this.todos.splice(this.todos.indexOf(todo), 1);
                        localStorage.setItem('tasks', JSON.stringify(this.todos));

                        this.$swal({
                            title: 'Deleted!',
                            text: 'Your task has been deleted.',
                            icon: 'success'
                        });
                    }
                });
            },

            // Edit Todo
            edit_todo(todo, index) {
                let item = $('.item');
                $(item[index]).attr('disabled', false);
                $(item[index]).focus();

                this.edited_tasks = todo;
                todo.completed = false;
            },
            done_edit(todo, index) {
                if(!this.edited_tasks) 
                {
                    return;
                }


                let item = $('.item');
                $(item[index]).attr('disabled', true);

                this.edited_tasks = null;
                this.is_edit = false;
                todo.content = todo.content.trim();
                todo.created_at = this.time_convert(this.get_timestamp());
                todo.timestamp = this.get_timestamp();
            },

            // Get timestamp
            get_timestamp() {
                const timestamp = new Date().getTime();

                return timestamp;
            },

             // Convert timestamp
            time_convert(timestamp) {
                const date = new Date(timestamp).toLocaleDateString();

                const time = new Date(timestamp).toLocaleTimeString();

                const converted = date + ' ' + time;

                return converted;
            },

            // Sort todos by date
            sort_by_date(type) {
                if(type === 'newest')
                {
                    this.todos.sort((a, b) => {
                        return (b.timestamp) - (a.timestamp);
                    });

                    return this.todos;
                }
                else if(type === 'oldest')
                {
                    this.todos.sort((a, b) => {
                        return (a.timestamp) - (b.timestamp);
                    });

                    return this.todos;
                }
            }
        },
    }
</script>

<style lang="scss" scoped>

    // Todo App Tasks
    .todo-app__tasks
    {
        width: 100%;
        height: 32rem;
        background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
        padding: 1rem;
        border-radius: 1.2rem;

        // Tasks Progress
        &-progress
        {
            width: 100%;
            height: auto;
            max-height: 7.5rem;
            background-color: var(--white);
            padding: 0.5rem;
            border-radius: 0.5rem;

            label
            {
               max-width: 100%;
               white-space: nowrap;
               text-overflow: ellipsis;
               overflow: hidden;

               span.title
               {
                   padding-right: 0.5rem;
               }
            }

            // Completed Progress Bar
            &__complete
            {
                display: flex;
                flex-direction: row;

                .progress
                {
                    width: 20rem;
                    max-width: 100%;
                    margin-top: 0.3rem;
                }
            }
        }

        // Tasks List
        &-list
        {
            width: 100%;
            min-height: 20rem;
            margin-top: 2rem;
            gap: 1rem;
            padding: 1rem !important;
            overflow: hidden;
            overflow-y: scroll;
            scrollbar-width: normal;
            scrollbar-color: rgba(255, 255, 255, 0.5) rgba(0, 0, 0, 0.2); 


            li
            {
                list-style: none;
                width: 100%;
                min-height: 4rem;
                background-color: var(--white);
                text-align: center;
                border-radius: 0.5rem;
                line-height: 3rem;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                opacity: 0;
                transform: translateX(-10rem) rotate(3deg);
                transition: all 0.5s cubic-bezier(.36,.12,.78,.58);
                cursor: pointer;
                padding-left: 1rem;
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                align-items: center;

                input[type="text"]
                {
                    min-width: 18rem;
                    padding: 0 0.5rem;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    -webkit-appearance: none;
                    border: none;
                    outline: none;
                    text-align: center;
                    background-color: transparent;
                    cursor: pointer;
                }

                // Checkbox
                input[type="checkbox"]
                {
                    -webkit-appearance: none;
                    width: 1.5rem;
                    height: 1.5rem;
                    border: 0.1rem solid #4facfe;
                    border-radius: 100rem;
                    cursor: pointer;
                    position: relative;
                }
                input[type="checkbox"]::before
                {
                    position: absolute;
                    content: '';
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    line-height: 1.5rem;
                    text-align: center;
                    color: var(--success);
                }
                input[type="checkbox"]:checked::before
                {
                    content: 'âœ”';
                }

                // Timestamp
                .created-at
                {
                    position: absolute;
                    left: 10%;
                    bottom: 0;
                    font-size: 0.6rem;
                    line-height: 1rem;
                    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
                    text-align: center;
                    padding: 0.2rem;
                    color: var(--white);
                }

                // Modify Tasks
                .modify-tasks
                {
                    position: absolute;
                    right: 0;
                    display: flex;
                    justify-content: center;
                    gap: 0.5rem;
                    padding: 0 1rem;
                    transform: translateX(5rem);
                    opacity: 0;
                    transition: all 0.5s ease;
                    font-size: 1.4rem;
        

                    .destroy-tasks
                    {
                        color: var(--danger);
                        cursor: pointer;
                    }
                    .edit-tasks
                    {
                        color: var(--blue);
                        cursor: pointer;
                    }
                }
            }

            // Show Tasks
            li.show
            {
               opacity: 1; 
               transform: translateX(0) rotate(0);
            }

            // Tasks Completed
            li.completed
            {
                text-decoration: line-through;
                text-decoration-color: var(--success);
            }

            li:hover
            {
                box-shadow: 0.5rem 0.5rem 0.5rem rgba(255, 255, 255, 0.2),
                            0.5rem 0.5rem 0.5rem rgba(0, 0, 0, 0.2);
                transform: translateY(-0.2rem);

                 .modify-tasks
                {
                    transform: translateX(0);
                    opacity: 1;
                }
            }
        }

        // Scroll Bar
        &-list::-webkit-scrollbar
        {
            width: 0.5vw;
        }

        &-list::-webkit-scrollbar-track
        {
            background-color: transparent;
            box-shadow: 0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.2),
                        -0.1rem -0.1rem 0.1rem rgba(0, 0, 0, 0.1),
                        inset 0.1rem 0.1rem 0.1rem rgba(255, 255, 255, 0.2),
                        inset -0.1rem -0.1rem 0.1rem rgba(0, 0, 0, 0.1);
            border-radius: 0.2rem;
        }

        &-list::-webkit-scrollbar-thumb
        {
            background-color: #4facfe;
            border-radius: 0.2rem;
        }
    }
</style>