<template>
    <div class="container d-flex justify-content-center">
        <!-- Todo App -->
        <div class="todo-app d-flex justify-content-center flex-column">
            <!-- Heading -->
            <h1 class="todo-app__heading">
                <span>Todo App</span>
            </h1>
            <!-- Heading -->

            <!-- Trigger Menu Button -->
            <div class="todo-app__btn-add" :class="{active: is_active}">
                <button class="btn btn-add-tasks"
                        @click="toggle()"
                        v-b-tooltip.hover.bottom="{ variant: 'light'}" title="Open Menu"
                >
                    <span>
                        <font-awesome-icon icon="fa-solid fa-plus" />
                    </span>
                </button>
            </div>
            <!-- Trigger Menu Button -->

            <!-- Todo App Box -->
            <div class="todo-app__box">
                <div class="todo-app__box-heading d-flex justify-content-between align-items-center">
                    <h2>
                        <font-awesome-icon icon="fa-solid fa-list" />
                        <span>Tasks List</span>
                    </h2>
                    <div class="todo-app__filter-tasks">
                        <b-dropdown class="dropdown-filter" right :text="filter_by" variant="white">
                            <b-dropdown-item class="dropdown-filter__item active">All Tasks</b-dropdown-item>
                            <b-dropdown-item class="dropdown-filter__item">Completed Tasks</b-dropdown-item>
                            <b-dropdown-item class="dropdown-filter__item">In-Progress Tasks</b-dropdown-item>
                            <b-dropdown-item class="dropdown-filter__item">By date (Newest)</b-dropdown-item>
                            <b-dropdown-item class="dropdown-filter__item">By date (Oldest)</b-dropdown-item>
                        </b-dropdown>
                    </div>
                </div>
                <!-- Todo List Component -->
                <todo-list :task_data="todos" :filter_data="filter_by"/>
            </div>
            <!-- Todo App Box -->

            <!-- Todo App Menu -->
            <div class="todo-app__menu d-flex flex-column align-items-center" :class="{active: is_active}">
                <!-- Menu Heading -->
                <div class="todo-app__menu-heading">
                    <h2>You have {{ todos.length }} tasks</h2>
                </div>
                <!-- Menu Heading -->

                <!-- Menu Input -->
                <div class="todo-app__menu-input">
                    <div class="input-group mb-3 d-flex flex-column justify-content-center align-items-center">
                        <span class="title">Add your tasks here:</span>
                        <input type="text" class="form-control" aria-label="" aria-describedby="" id="add-tasks"
                               placeholder="Enter New Tasks"
                               v-model="new_todo"
                               @keydown.enter="add_todo()"
                        >

                        <!-- Add Task Button -->
                        <button class="btn btn-add"
                                @click="add_todo()"
                        >
                            <span>Add</span>
                            <font-awesome-icon icon="fa-solid fa-plus" />
                        </button>
                        <!-- Add Task Button -->

                    </div>

                    <!-- Complete/Destroy Button -->
                    <div class="todo-app__menu-modify-tasks d-flex justify-content-center align-items-center flex-column">
                        <button class="btn btn-complete-all"
                                @click="complete_all()"
                        >
                            <span>Complete All</span>
                        </button>
                        <button class="btn btn-destroy-all"
                                @click="destroy_all()"
                        >
                            <span>Delete All</span>
                        </button>
                    </div>
                    <!-- Complete/Destroy Button -->
                </div>
                <!-- Menu Input -->
            </div>
        </div>
    </div>
</template>

<script>
import $ from 'jquery';
import todo_list from './todo_list.vue';

export default {
    data() {
       return {
          is_active: false,
          new_todo: '',
          todos: [],
          filter_by: 'All Tasks',
       }
    },
    created() {
        // Press ESC key to open Menu    
       document.addEventListener('keydown', (e) => {
            if(e.keyCode === 27)
            {
                this.is_active = !this.is_active
            }
        });

        // Local Storage
        if(localStorage.getItem('tasks')) 
        {
            try
            {
                this.todos = JSON.parse(localStorage.getItem('tasks'));
            }
            catch(e)
            {
                localStorage.removeItem('tasks');
            }
        }
   },
   mounted() {
        // Dropdown filter show    
        this.$root.$on('bv::dropdown::show', bvEvent => {
           const children = bvEvent.target.children;
           for(let i = 0; i < children.length; i++)
           {
               $(children[i]).find('a').removeAttr('href').css('cursor', 'pointer');
               $(children[i]).click(() => {
                   const text = $(children[i]).text();

                   this.filter_by = text;
               });  
           }
       });

        // Dropdown filter active
       for(let i = 0; i < $('.dropdown-filter__item').length; i++)
       {
           const item = $('.dropdown-filter__item')[i];

           $(item).click(() => {
               this.active_filter();
               
               $(item).addClass('active');
           });
       }
   },
   methods: {
       // Trigger Menu    
        toggle() {
           this.is_active = !this.is_active;
           if(this.is_active)
           {
               $('#add-tasks').attr('autofocus', true);
           }
           else
           {
               $('#add-tasks').attr('autofocus', false);
           }
       },

       // Add Todo    
        add_todo() {
           if(this.new_todo === '')
           {
               this.$swal({
                   icon: 'error',
                   title: 'Oops...',
                   text: 'Please Enter Your Tasks!',
               });
           }
           else
           {
                this.new_todo = this.html_entities_decode(this.new_todo);
                this.todos.push({
                    content: this.new_todo, created_at: this.time_convert(this.get_timestamp()), timestamp: this.get_timestamp(), completed: false
                });
           }

           this.new_todo = '';
           localStorage.setItem('tasks', JSON.stringify(this.todos));
       },

       // Complete All Todo    
        complete_all() {
           for(let i = 0; i < this.todos.length; i++)
           {
               this.todos[i].completed = true;
           }
       },

       // Destroy All Todo 
        destroy_all() {
           this.$swal({
               title: 'Are you sure?',
               text: "You won't be able to revert this!",
               icon: 'warning',
               showCancelButton: true,
               confirmButtonColor: '#3085d6',
               cancelButtonColor: '#d33',
               confirmButtonText: 'Yes, delete it!'
           }).then((result) => {
               if(result.isConfirmed)
               {
                   this.todos.splice(0, this.todos.length);
                   localStorage.setItem('tasks', this.todos);

                   this.$swal({
                       title: 'Deleted!',
                       text: 'All tasks has been deleted.',
                       icon: 'success'
                   });
               }
           });
       },

        // HTML decode
        html_entities_decode(string) {
            if(string && typeof string === 'string')
            {
                string = string.replace(/<\/?\w(?:[^"'>]|"[^"]*"|'[^']*')*>/gmi, '');
                string = string.replace(/<script[^>]*>([\s\S]*?)<\/script>/gmi, '');
            }

            return string;
        },

        // Dropdown filter active
        active_filter() {
           for(let i = 0; i < $('.dropdown-filter__item').length; i++)
            {
                const item = $('.dropdown-filter__item')[i];

                $(item).removeClass('active');
            }
        },

        // Get timestamp
        get_timestamp() {
            const timestamp = new Date().getTime();

            return timestamp;
        },
        
        // Convert timestamp
        time_convert(timestamp) {
            const date = new Date(timestamp).toLocaleDateString();

            const time = new Date(timestamp).toLocaleTimeString();

            const converted = date + ' ' + time;

            return converted;
        }
   },
   components: {
       'todo-list': todo_list,
   }
}
</script>

<style lang="scss" scoped>
    .btn
    {
        border: none;
        outline: none;
    }
    .btn.focus, .btn:focus
    {
        border: none !important;
        outline: none !important;
        box-shadow: none !important;
    }

    // Todo App
    .todo-app
    {
        width: 35rem;
        height: 40rem;
        background-color: var(--white);
        box-shadow: -5rem -5rem 10rem rgba(255, 255, 255, 0.5),
                    5rem 5rem 7rem rgba(70, 70, 70, 0.12),
                    0 -5rem 10rem rgba(255, 255, 255, 0.5),
                    0 5rem 7rem rgba(70, 70, 70, 0.12),
                    0 5rem 10rem rgba(255, 255, 255, 0.5),
                    0 -5rem 7rem rgba(70, 70, 70, 0.12);
        border-radius: 1.6rem;
        overflow: hidden;
        position: relative;

        // Todo App Heading
        &__heading
        {
            font-size: 2rem;
            padding: 1rem;
            width: 100%;
            height: 3rem;
            background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
            text-align: center;
            line-height: 1rem;
            color: var(--white);
            margin-bottom: 0;
        }

        // Trigger Menu Button
        &__btn-add
        {
            position: absolute;
            left: 0rem;
            bottom: 3rem;
            min-width: 4rem;
            min-height: 4rem;
            border: none;
            outline: none;
            border-radius: 100rem;
            background-color: var(--white);
            box-shadow: -0.5rem -0.5rem 1rem rgba(255, 255, 255, 0.2),
                        0.5rem 0.5rem 1rem rgba(70, 70, 70, 0.12),
                        0 -0.5rem 1rem rgba(255, 255, 255, 0.2),
                        0 0.5rem 1rem rgba(70, 70, 70, 0.12),
                        0 0.5rem 1rem rgba(255, 255, 255, 0.2),
                        0 -0.5rem 1rem rgba(70, 70, 70, 0.12);
            overflow: hidden;
            z-index: 101;

            span
            {
                svg
                {
                    transition: all 0.5s ease;
                }
            }

            .btn
            {
                min-width: 2.8rem;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                border-radius: 100rem;
                background-color: var(--white);
                color: var(--gray);
                box-shadow: -0.2rem -0.2rem 0.2rem rgba(255, 255, 255, 0.5),
                            0.2rem 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                            0 -0.2rem 0.2rem rgba(255, 255, 255, 0.5),
                            0 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                            0 0.2rem 0.2rem rgba(255, 255, 255, 0.5),
                            0 -0.2rem 0.2rem rgba(70, 70, 70, 0.12);
                font-size: 1.4rem;
                transition: all 0.5s ease;
                z-index: 5;
            }

            .btn:hover
            {
                color: var(--blue);
            }

            .btn:active
            {
                color: var(--blue);
                box-shadow: inset -0.2rem -0.2rem 0.2rem rgba(255, 255, 255, 0.5),
                            inset 0.2rem 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                            inset 0 -0.2rem 0.2rem rgba(255, 255, 255, 0.5),
                            inset 0 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                            inset 0 0.2rem 0.2rem rgba(255, 255, 255, 0.5),
                            inset 0 -0.2rem 0.2rem rgba(70, 70, 70, 0.12);
            }
        }

        &__btn-add.active
        {
            span
            {
                svg
                {
                    transform: rotate(45deg);
                }
            }
        }

        &__btn-add::after
        {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
            filter: blur(0.1rem);
            animation: rotate 2s linear infinite;
        }
        @keyframes rotate 
        {
            0%
            {
                transform: rotate(0);
            }  
            100%
            {
                transform: rotate(360deg);
            }
        }

        // Todo App Box
        &__box
        {
            width: 100%;
            height: 100%;
            padding: 1.5rem;

            // Todo App Box Heading
            &-heading
            {
                display: inline-block;
                

                h2
                {
                    font-size: 1.4rem;

                    svg
                    {
                        padding-right: 0.5rem;
                    }
                }
            }
        }

        // Todo App Menu
        &__menu
        {
            position: absolute;
            top: 60%;
            right: 40%;
            width: 100%;
            height: 100%;
            background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
            transform: scale3d(0.2, 0.2, 0.2);
            opacity: 0;
            border-top-right-radius: 100rem;
            overflow: hidden;
            transition: all 0.5s ease;
            z-index: 99;

            // Todo App Menu Heading
            &-heading
            {
                padding: 1rem;
                
                h2
                {
                    font-size: 1.4rem;
                    color: var(--white);
                }
            }

            // Todo App Input
            &-input
            {   color: var(--white);
                transform: scale3d(0, 0, 0);
                opacity: 0;

                .input-group
                {
                    gap: 1rem;
                }

                .title
                {
                    font-size: 1.4rem;
                    padding: 0.5rem 0;
                    text-shadow: -0.1rem -0.1rem 0.1rem rgba(255, 255, 255, 0.5),
                                 0.1rem 0.1rem 0.1rem rgba(70, 70, 70, 0.12),
                                 0 -0.1rem 0.1rem rgba(255, 255, 255, 0.5),
                                 0 0.1rem 0.1rem rgba(70, 70, 70, 0.12),
                                 0 0.1rem 0.1rem rgba(255, 255, 255, 0.5),
                                 0 -0.1rem 0.1rem rgba(70, 70, 70, 0.12);
                }

                input
                {
                    background-color: transparent;
                    min-width: 20rem;
                    border: none;
                    outline: none;
                    border-radius: 0.5rem !important;
                    box-shadow: -0.2rem -0.2rem 0.5rem rgba(255, 255, 255, 0.2),
                                 0.2rem 0.2rem 0.5rem rgba(0, 0, 0, 0.1),
                                 inset -0.5rem -0.5rem 0.5rem rgba(255, 255, 255, 0.2),
                                 inset 0.5rem 0.5rem 0.5rem rgba(0, 0, 0, 0.1);
                    color: var(--white);
                }

                input::placeholder
                {
                    color: var(--white);
                }

                // Button
                .btn
                {
                    color: var(--gray);
                    min-width: 5rem;
                    background-color: transparent;
                    box-shadow: -0.2rem -0.2rem 0.2rem rgba(255, 255, 255, 0.2),
                                0.2rem 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                                0 -0.2rem 0.2rem rgba(255, 255, 255, 0.2),
                                0 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                                0 0.2rem 0.2rem rgba(255, 255, 255, 0.2),
                                0 -0.2rem 0.2rem rgba(70, 70, 70, 0.12);
                    span
                    {
                        padding-right: 0.5rem;
                    }
                }

                .btn:hover
                {
                    color: var(--white);
                }

                .btn:active
                {
                   box-shadow: inset -0.2rem -0.2rem 0.2rem rgba(255, 255, 255, 0.2),
                               inset 0.2rem 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                               inset 0 -0.2rem 0.2rem rgba(255, 255, 255, 0.2),
                               inset 0 0.2rem 0.2rem rgba(70, 70, 70, 0.12),
                               inset 0 0.2rem 0.2rem rgba(255, 255, 255, 0.2),
                               inset 0 -0.2rem 0.2rem rgba(70, 70, 70, 0.12);
                }
            }

            // Modify
            &-modify-tasks
            {
                gap: 1rem;
                margin-top: 5rem;
            }
        }

        // Active menu
        &__menu.active
        {
            top: 0;
            right: 0;
            transform: scale3d(1, 1, 1);
            opacity: 1;
            border-top-right-radius: 0;

            .todo-app__menu-input
            {
                transform: scale3d(1, 1, 1);
                opacity: 1;
            }
        }

        // Filter Tasks
        &__filter-tasks
        {
            .dropdown-filter
            {
                min-width: 14rem;
                height: 2rem;
                box-shadow: 0.2rem 0.2rem 0.5rem rgba(255, 255, 255, 0.3),
                            -0.2rem -0.2rem 0.5rem rgba(92, 89, 89, 0.12),
                            0 0.2rem 0.5rem rgba(255, 255, 255, 0.3),
                            0 0.2rem 0.5rem rgba(92, 89, 89, 0.12),
                            inset 0.2rem 0.2rem 0.3rem rgba(92, 89, 89, 0.12),
                            inset -0.2rem -0.2rem 0.3rem rgba(117, 115, 115, 0.12);

                &__item
                {
                    margin: 0.5rem;
                    border-radius: 0.2rem;
                    padding: 0.2rem;
                }
                &__item.active
                {
                    background-color: #4facfe;
                    pointer-events: none;
                }
            }
        }
    }
</style>